import React, {useCallback} from 'react';
import './style.scss'
import {useHistory} from "react-router";
import {useDispatch} from "react-redux";
import {openMenu} from "../../../store/menu/actions";

export const Footer = () => {
    const dispatch = useDispatch();
    const setName = useCallback(() => {
        dispatch(openMenu())
    }, [dispatch]);
    const {push}=useHistory()
    const handlePushHome=(e) => {
        e.preventDefault()
        push('/')
    }
    const handlePushAbout=(e) => {
        e.preventDefault()
        push('/about')
    }
    const handlePushAllNews=(e) => {
        e.preventDefault()
        push('/allNews')
    }
    const handlePushRev=(e) => {
        e.preventDefault()
        push('/review')
    }
    const handlePushCont=(e) => {
        e.preventDefault()
        push('/contact')
    }
    return (
        <footer className="footer">
            <div className="clasic_footer">
                <div className="containerP">
                    <div className="clasic_footer_left">
                        <a href="/" className="logo">
                            <img src="/images/logo.png" alt=""/>
                        </a>
                        <div className="footer_descr">
                            <p>      ,  em ipsum dolor sit amet, consectetur
                                adipiscing elit, sed do eiusmod tempor incididunt ut labore et</p>
                        </div>
                    </div>
                    <div className="clasic_footer_menu">
                        <div className="clasic_footer_title"></div>
                        <div className="clasic_footer_nav">
                            <ul>
                                <li>
                                    <a href="/" onClick={handlePushHome}></a>
                                </li>
                                <li>
                                    <a href="/" onClick={handlePushAbout}> </a>
                                </li>
                                <li>
                                    <a href="/"></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="clasic_footer_menu">
                        <div className="clasic_footer_title"></div>
                        <div className="clasic_footer_nav">
                            <ul>
                                <li>
                                    <a href="/" onClick={handlePushRev}></a>
                                </li>
                                <li>
                                    <a href="/" onClick={handlePushAllNews}></a>
                                </li>
                                <li>
                                    <a href="/" onClick={setName} ></a>
                                </li>
                                <li>
                                    <a href="/" onClick={handlePushCont} ></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="clasic_footer_menu">
                        <div className="clasic_footer_title"></div>
                        <div className="clasic_footer_nav">
                            <ul>
                                <li>
                                    <a href="/"></a>
                                </li>
                                <li>
                                    <a href="/"> 0234</a>
                                </li>
                                <li>
                                    <a href="/"> </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div className="under_footer">
                <div className="containerP">
                    <div className="under_footer_left">
                        <div className="copyr"> 2011-2020 Pride.io   .</div>
                    </div>
                    <div className="under_footer_right">
                        <ul>
                            <li>
                                <a href="/" className="fb"> </a>
                            </li>
                            {/* <li>*/}
                            {/*    <a href="#" class="m"></a>*/}
                            {/*</li> */}
                            <li>
                                <a href="/" className="telegram"> </a>
                            </li>
                            <li>
                                <a href="/" className="btc"> </a>
                            </li>
                            <li>
                                <a href="/" className="tw"> </a>
                            </li>
                            <li>
                                <a href="/" className="inst"> </a>
                            </li>
                            <li>
                                <a href="/" className="gith"> </a>
                            </li>

                        </ul>
                    </div>
                </div>
            </div>
        </footer>
    );
};

